
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';
export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
Alert,
XDialog
},
data() {
return {
value1: '',
startTime:"",
endTime:"",
hourListValue:'',
hot:'',
activeType:'',
activityName:'',
activePace:'',
activeTitle:'',
activeCreatePeople:'',
activePrincipalPeople:'',
activeContext:'',
activeContent:'',
activeStatus:0,
activeProjectActive:'',
PickerVisible1:false,
pickerValue1:'请选择活动类型',
years:'',
year1: [''],
list:'',
list1:'',
departmentidItem:[],
PickerVisible2:false,
PickerVisible3:false,
PickerVisible5:false,
departmentid:this.$store.getters.user.departmentid,
showQrcodeDialog: false,
Qrcode:'',
departmentname1:'',
departmentidId:'',
departmentidNames:'',
str:[],String:'',
picList:{list:[],arr:[]},
qrsrc: '',
qrBoxShow: false
};
},
methods: {
backRoute(){
setTimeout(() => history.back(), 500);
},
openPicker() {
this.$refs.picker.open();
},
handleConfirm(d) {
this.startTime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + '';
},
handlePicker(){
this.PickerVisible2=!this.PickerVisible2;
axios({
method: 'get',
url: 'pscoredetail/queryByJoinList'
}) .then((res)=> {
var t = res.data;
t.forEach((value)=>{
value.title = value.title.substring(0,4);
});
this.list = t;
}).catch(function (error) {
console.log(error);
});
},
handlePicker1(){
this.PickerVisible5=!this.PickerVisible5;
axios({
method: 'get',
url: 'pdepartment/getList'
}) .then((res)=> {
this.list1= res.data;
}).catch(function (error) {
console.log(error);
});
},
onValuesChange(picker, values){
this.pickerValue1=values[0];
this.PickerVisible1=false
},
submit(){

if(!this.startTime){
return this.$vux.toast.show({
text: '填写开始时间',
type: 'text'
});
}

if(!this.endTime){
return this.$vux.toast.show({
text: '填写结束时间',
type: 'text'
});
}

if(!this.activityName){
return this.$vux.toast.show({
text: '选择活动类型',
type: 'text'
});
}

if(!this.activeTitle){
return this.$vux.toast.show({
text: '选择活动类型',
type: 'text'
});
}

if(!this.activePace){
return this.$vux.toast.show({
text: '填写活动地点',
type: 'text'
});
}

if(!this.activePrincipalPeople){
return this.$vux.toast.show({
text: '填写负责人',
type: 'text'
});
}

if(!this.activeContext){
return this.$vux.toast.show({
text: '填写活动内容',
type: 'text'
});
}


var starttime = this.startTime.replace(new RegExp("-","gm"),"/");
var starttimeHaoMiao = (new Date(starttime)).getTime();
var endtime = this.endTime.replace(new RegExp("-","gm"),"/");
var endtimeHaoMiao = (new Date(endtime)).getTime();

if(starttimeHaoMiao<endtimeHaoMiao){
axios({
method: 'post',
url: 'active/create',
params: {
startTime:starttimeHaoMiao,
endTime:endtimeHaoMiao,
activeType:this.activeType,
activityProjectId:this.activeProjectActive,
activePace:this.activePace,
activeCreatePeople:this.$store.getters.user.userid,
activePrincipalPeople:this.activePrincipalPeople,
activeContext:this.activeContext,
activeName:this.activeTitle,
activeStatus:1,
departmentid:this.departmentidId.join(),
picids:this.picList.arr.join() || '780'
}
}).then((res)=> {






this.showQR(res.data);
alert(res.data);
}).catch(function (error) {
console.log(error);
this.$vux.toast.show({
text:进入失败,
type: 'text'
});
});
}else {
this.$vux.toast.show({
text: '开始日期不能大于结束日期',
type: 'text'
});
}
},
showQR(data){
alert(1);



this.qrBoxShow = true;
this.qrsrc = `http://www.dlbdata.cn/dangjian/active/showQrCode?activeId=${data}`;
alert(3);
},
submit1(it){
this.activeType=it.id;
this.activeProjectActive = it.projectId;
this.activityName = it.title;
this.PickerVisible2=false

},
submit2(it){

var arr = this.departmentidItem;

if(this.departmentidId.indexOf(it.departmentid)==-1){
arr.push(it)
}

let ss=[];
let ssTemp=[];
for(var i= 0 ;i<arr.length;i++){
ss.push(arr[i].departmentid);
ssTemp.push(arr[i].departmentname);
}
this.departmentidId = ss;

this.departmentidNames = ssTemp.join();

this.PickerVisible5=false;

},
resetDepartment(){
this.departmentidItem = [];
this.departmentidNames = "";
this.departmentidId=[];
this.PickerVisible5=false;
},
getActivity(){
},
log (str1, str2 = '') {
console.log(str1, str2)
},
showPlugin () {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm (val) {
console.log('plugin confirm', val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
toggleFormat () {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm'
},
change (value) {
this.startTime = value;
},
change1 (value) {
this.endTime = value;
},
clearValue (value) {
this.value6 = ''
},
clearValue8 (value) {
this.value8 = ''
},
setToday (value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok')
},
chooseImage(it) {
wx.chooseImage({
count: 1, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId => wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => {
let promiseList = [];
serverIds.map(serverId => promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
));
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data));
it.list.push("http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=" + pictureIds.join());
it.arr.push(pictureIds.join());
});
});
}
});
}
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
this.getActivity()
}
};



